import tableData from "../assets/list.json";

export default {
  name: "HomeView",
  components: {},
  mounted() {
    this.getShips_();
  },
  computed: {
    resultQuery() {
      // if (this.search) {

      return tableData.filter((item) => {
        const equipment = this.equipment;

        if (equipment === "all" || equipment === null) {
          if (this.search === "") {
            return true;
          } else {
            return this.search
              .toLowerCase()
              .split(" ")
              .every((v) => {
                return item.tagname.toLowerCase().includes(v);
              });
          }
        } else {
          if (this.search === "") {
            if ((item.equipid === +equipment) === true) {
              return true;
            } else {
              return false;
            }
          } else {
            return this.search
              .toLowerCase()
              .split(" ")
              .every((v) => {
                const tagname = item.tagname.toLowerCase().includes(v);
                if (
                  (item.equipid === +equipment) === true &&
                  tagname === true
                ) {
                  return true;
                } else {
                  return false;
                }

                // return item.tagdesc.toLowerCase().includes(v);
              });
          }
        }
      });
      // } else {
      //   return tableData;
      // }
    },

    // filteredList() {
    //   return this.desserts.filter((des) => {
    //     return des.tagdesc.toLowerCase().includes(this.search.toLowerCase());
    //   });
    // },
  },
  methods: {
    getShips_() {
      this.ships = [
        { shipid: "1", shipname: "1" },
        { shipid: "2", shipname: "2" },
        { shipid: "3", shipname: "3" },
        { shipid: "4", shipname: "4" },
        { shipid: "5", shipname: "5" },
        { shipid: "5", shipname: "6" },
        { shipid: "5", shipname: "7" },
        { shipid: "5", shipname: "8" },
        { shipid: "5", shipname: "9" },
      ];

      const eqSet = new Set();
      eqSet.add("all");
      tableData.map((item) => {
        eqSet.add(item.equipid);
      });
      this.equipments = Array.from(eqSet);
    },
  },
  data() {
    return {
      //
      ship: null,
      equipment: null,
      search: "",
      time: null,
      ships: [],
      times: [" 10", " 30"],
      equipments: [],

      //
      tab: null,
      items: ["Trend Chart", "Tag View"],
      text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
      desserts: [],
      headers: [
        { text: "", align: "center", value: "glutenfree" },
        {
          text: "NO",
          align: "center",
          value: "no",
        },
        { text: "", align: "center", value: "shipname" },
        { text: "ID", align: "center", value: "equipid" },
        { text: "", align: "center", value: "tagname" },
        { text: "Description", align: "center", value: "tagdesc" },
      ],
    };
  },
};
